import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
import SimpleSearch from "./table/SimpleSearch";
import "./table/home.css";
// import { Url } from "./url";
export default function HomeNewCus(props) {
    const Url = props.myToken;
    // const[Url,setUrl]=useState();
    const navigate = useNavigate();
    const { id, idof } = props;
    const [car_info, setCar_info] = useState([
        {
            car_id: "",
            owner_id: "",
            owner_name: "",
            gender: "",
            phone_no: "",
            plate_no: "",
            car_model: "",
            km: "",
        },
    ]);
    const [owner_info, setOwner_info] = useState([
        {
            car_id: "",
            owner_id: "",
            owner_name: "",
            gender: "",
            phone_no: "",
            plate_no: "",
            car_model: "",
            km: "",
        },
    ]);
    const [owner_id, setOwner_id] = useState("");
    const [owner_name, setOwner_name] = useState("");
    const [gender, setGender] = useState("");
    const [phone_no, setPhone_no] = useState("");
    const [car_id, setCar_id] = useState("");
    const [plate_no, setPlate_no] = useState("");
    const [car_model, setCar_model] = useState("");
    const [km, setKm] = useState("");
    const [userStatus, setOwnerStatus] = useState([["message", "non"]]);
    const [carStatus, setCarStatus] = useState([["message", "non"]]);
    const [latestCars, setLatestCars] = useState([""]);
    const [clicked, setclicked] = useState(0);
    const [sidebar, setsidebar] = useState(0);
    // const [jobStatus, setJobStatus] = useState([
    //     ["status", "NOT REGISTERED"],
    //     ["Cus. ID", "unset"],
    //     ["message", "non"],
    // ]);
    // const [dispcus, setdispcus] = useState(1);
    /**display selected ( int car_id ,int owner_id ,"CAR" || "OWNER" ) */
    const displaySelected = async (car_id, owner_id, searchFor) => {
        // console.log(car_id, owner_id, searchFor);
        setclicked(1);
        setsidebar(false);
        // console.log(searchFor);
        if (searchFor == "CAR") {
            // try {
            // console.log("Car");
            const url = `${Url}/find/${searchFor}/ID/${car_id}`;

            // const { data } = await
            axios
                .get(url)
                .then((res) => {
                    const { data } = res;
                    setCar_info(data);
                    // setOwner_id(data[0].owner_id);
                    // setOwner_name(data[0].owner_name);
                    // setGender(data[0].gender);
                    // setPhone_no(data[0].phone_no);
                    setCar_id(data[0].car_id);
                    setPlate_no(data[0].plate_no);
                    setCar_model(data[0].car_model);
                    setKm(data[0].km);
                    setCarStatus([["message", "car found"]]);
                })
                .catch((err) => {
                    // setCar_info(data);
                    // setOwner_id(data[0].owner_id);
                    // setOwner_name(data[0].owner_name);
                    // setGender(data[0].gender);
                    // setPhone_no(data[0].phone_no);
                    setCar_info([
                        {
                            car_id: "",
                            owner_id: "",
                            owner_name: "",
                            gender: "",
                            phone_no: "",
                            plate_no: "",
                            car_model: "",
                            km: "",
                        },
                    ]);
                    setCar_id("");
                    // setPlate_no("");
                    setCar_model("");
                    // setKm("");
                    setCarStatus([["message", "no car found"]]);
                })
                .finally(() => {
                    setclicked(0);
                });
            // } catch (err) {
            //     console.log(err);
            //     setclicked(0);
            // }
        } else if (searchFor == "OWNER") {
            // try {
            const url = `${Url}/find/${searchFor}/ID/${owner_id}`;
            // const { data } = await
            axios
                .get(url)
                .then((res) => {
                    const { data } = res;
                    // console.log(data);
                    setOwner_info(data);
                    setOwner_id(data[0]?.owner_id);
                    setOwner_name(data[0].owner_name ? data[0].owner_name : "");
                    setGender(data[0].gender ? data[0].gender : "");
                    setPhone_no(data[0].phone_no);

                    setOwnerStatus([["message", "user found"]]);
                    // setCar_id(data[0].car_id);
                })
                .catch((err) => {
                    setOwner_info([
                        {
                            car_id: "",
                            owner_id: "",
                            owner_name: "",
                            gender: "",
                            phone_no: "",
                            plate_no: "",
                            car_model: "",
                            km: "",
                        },
                    ]);
                    setOwner_id("");
                    setOwner_name("");
                    setGender("");
                    // setPhone_no("");
                    setOwnerStatus([["message", "user not found"]]);
                })
                .finally(() => {
                    setclicked(0);
                });
            // setPlate_no(data[0].plate_no);
            // setCar_model(data[0].car_model);
            // setKm(data[0].km);
            // } catch (err) {
            //     console.log(err);
            //     setclicked(0);
            // }
        } else setclicked(0);
    };
    useEffect(() => {
        // displaySelected(car_id, "", "CAR");
        findLatestCars();
        // console.log("found latest cars");
    }, []);
    useEffect(() => {
        //console.log("yyy", id, idof);
        displaySelected(id, id, idof);
    }, [id, idof]);
    // useEffect(() => {
    //     setOwnerStatus([["message", "non"]]);
    //     setCarStatus([["message", "non"]]);
    // }, [owner_info, car_info, owner_id, car_id]);
    // useEffect(() => {
    //     setOwnerStatus([["message", "non"]]);
    //     setCarStatus([["message", "non"]]);
    //     findLatestCars();
    // }, [car_id]);
    const findLatestCars = () => {
        // find latest cars
        axios
            .get(`${Url}/get/latestCars/limit/30`)
            .then((res) => {
                if (res.data) {
                    setLatestCars(res.data);
                    // console.log("yared yyyy", res);
                } else console.log("no resr", res);
            })
            .catch((err) => {
                console.log(err);
                if (err.response.status == "401") {
                    //window.location.reload(false);
                    navigate("/login");
                    // window.location = "/login";
                }
            });
    };
    const handleSubmit = async (event) => {
        event.preventDefault();
        if (phone_no && plate_no && km) {
            if (!clicked) {
                setclicked(1);
                try {
                    const url = `${Url}/edit/carandowner`;
                    // const { data } = await
                    axios
                        .post(url, {
                            car_id,
                            owner_id,
                            car_model,
                            plate_no,
                            km,
                            phone_no,
                            owner_name,
                            gender,
                        })
                        .then((res) => {
                            const { data } = res;
                            if (owner_id && car_id) {
                                if (data.affectedRows > 0) {
                                    setCarStatus([["message", data.info]]);
                                    displaySelected(car_id, "", "CAR");
                                    displaySelected(car_id, owner_id, "OWNER");
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setCarStatus([
                                        ["message", data.sqlMessage],
                                    ]);
                                }
                            } else if (owner_id) {
                                if (data.affectedRows > 0) {
                                    setCarStatus([["message", data.info]]);
                                    // displaySelected(car_id, "", "CAR");
                                    displaySelected("", owner_id, "OWNER");
                                    setCar_id(data?.insertId);
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setCarStatus([
                                        ["message", data?.sqlMessage],
                                    ]);
                                }
                            } else if (car_id) {
                                if (data[0]?.affectedRows > 0) {
                                    setOwnerStatus([
                                        ["message", data[0]?.info],
                                    ]);
                                    setOwner_id(data[0]?.insertId);
                                    displaySelected(
                                        car_id,
                                        data[0]?.insertId,
                                        "OWNER"
                                    );
                                    // displaySelected(car_id, "", "CAR");
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setOwnerStatus([
                                        ["message", data[0]?.sqlMessage],
                                    ]);
                                }
                                if (data[1]?.affectedRows > 0) {
                                    setCarStatus([["message", data[1]?.info]]);
                                    // displaySelected(car_id, "", "CAR");
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setCarStatus([
                                        ["message", data[1]?.sqlMessage],
                                    ]);
                                }
                            } else {
                                if (data[0]?.affectedRows > 0) {
                                    setOwnerStatus([
                                        ["message", data[0]?.info],
                                    ]);
                                    setOwner_id(data[0]?.insertId);
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setOwnerStatus([
                                        ["message", data[0]?.sqlMessage],
                                    ]);
                                }
                                if (data[1]?.affectedRows > 0) {
                                    setCarStatus([["message", data[1]?.info]]);
                                    setCar_id(data[1]?.insertId);
                                    // displaySelected(car_id, "", "CAR");
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setCarStatus([
                                        ["message", data[1]?.sqlMessage],
                                    ]);
                                }
                            }
                        })
                        .catch((err) => {
                            console.log(err);
                            setCarStatus([["message", "error1"]]);
                        })
                        .finally(() => {
                            setclicked(0);
                        });
                    // console.log(data);
                } catch (err) {
                    // console.log(data);
                    console.log(err);
                    setclicked(0);
                    setCarStatus([["message", "error2"]]);
                }
            }
        } else {
            alert("Fill all required forms!");
        }
    };
    const handleSubmitUser = async (event) => {
        event.preventDefault();

        if (!clicked) {
            setclicked(1);
            if (event.nativeEvent.submitter.value === "check") {
                //check clicked
                if (phone_no) {
                    // try {
                    const url = `${Url}/find/OWNER/number/${phone_no}`;
                    // /:token/find/:searchfor/number/:number"
                    // const { data } = await
                    axios
                        .get(url)
                        .then((res) => {
                            // console.log("found user", res);
                            const { data } = res;
                            // console.log(data.length, data[0]);
                            if (Array.isArray(data)) {
                                if (data.length) {
                                    // setOwner_info(data[0]);
                                    // setOwner_id(data[0].owner_id);
                                    // setOwner_name(data[0].owner_name);
                                    // setGender(data[0].gender);
                                    // setPhone_no(data[0].phone_no);
                                    displaySelected(
                                        data[0].owner_id,
                                        data[0].owner_id,
                                        "OWNER"
                                    );
                                } else {
                                    displaySelected(
                                        data[0].owner_id,
                                        data[0].owner_id,
                                        "OWNER"
                                    );
                                    setOwner_info("");
                                    setOwner_id("");
                                    setOwnerStatus([["message", "Not Found"]]);
                                }
                            } else {
                                setOwnerStatus([["message", "Not Found"]]);
                            }
                        })
                        .catch((err) => {
                            console.log("error5", err);
                            setOwner_id("");
                            displaySelected("", "", "OWNER");
                            setOwnerStatus([["message", "Not Found"]]);
                        })
                        .finally(() => {
                            setclicked(0);
                        });

                    // if (data.affectedRows > 0) {
                    //     setCarStatus([["message", data.info]]);
                    // } else {
                    //     // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                    //     setCarStatus([["message", data.sqlMessage]]);
                    // }
                    // } catch (err) {
                    //     // console.log(data);
                    //     console.log(err);
                    //     setCarStatus([["message", "non"]]);
                    //     setclicked(0);
                    // }
                }
            } // submit clicked
            else {
                if (owner_id) {
                    // console.log("user updated");
                    try {
                        const url = `${Url}/edit/owner`;
                        // const { data } = await
                        axios
                            .post(url, {
                                owner_id: owner_id,
                                owner_name: owner_name,
                                gender: gender,
                                phone_no: phone_no,
                            })
                            .then((res) => {
                                const { data } = res;
                                if (data.affectedRows > 0) {
                                    setOwnerStatus([["message", data.info]]);
                                    findLatestCars();
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setOwnerStatus([
                                        ["message", data.sqlMessage],
                                    ]);
                                    // console.log(data);
                                }
                            })
                            .catch((err) => {
                                console.log(err);
                            })
                            .finally(() => {
                                setclicked(0);
                            });
                    } catch (err) {
                        console.log(err);
                        setclicked(0);
                    }
                } else {
                    try {
                        const url = `${Url}/new/owner`;
                        // const { data } = await
                        axios
                            .post(url, {
                                owner_name: owner_name,
                                gender: gender,
                                phone_no: phone_no,
                            })
                            .then((res) => {
                                const { data } = res;
                                if (data.insertId > 0) {
                                    setOwnerStatus([["message", "non"]]);
                                    setOwner_id(data.insertId);
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setOwnerStatus([
                                        ["message", data.sqlMessage],
                                    ]);
                                    // console.log(data);
                                }
                            })
                            .catch((err) => {
                                console.log(err);
                            })
                            .finally(() => {
                                setclicked(0);
                            });
                    } catch (err) {
                        console.log(err);
                        setclicked(0);
                    }
                }
            }
        }
    };
    const handleSubmitCar = async (event) => {
        event.preventDefault();
        if (!clicked) {
            setclicked(1);
            // console.log(event.nativeEvent.submitter.value);
            if (event.nativeEvent.submitter.value === "check") {
                if (plate_no) {
                    // try {
                    const url = `${Url}/find/CAR/number/${plate_no}`;
                    // /:token/find/:searchfor/number/:number"
                    // const { data } = await
                    axios
                        .get(url)
                        .then((res) => {
                            // console.log("found car", res);
                            // res.data
                            const { data } = res;
                            if (Array.isArray(data)) {
                                if (data.length) {
                                    // console.log(data[0]);
                                    // setCar_info(data);
                                    // setCar_id(data[0].car_id);
                                    // setPlate_no(data[0].plate_no);
                                    // setCar_model(data[0].car_model);
                                    // // setOwner_id(data[0].owner_id);
                                    // setKm(data[0].km);
                                    displaySelected(
                                        data[0].car_id,
                                        data[0].car_id,
                                        "CAR"
                                    );
                                    setCarStatus([["message", "Found car"]]);
                                } else {
                                    //not found
                                    setCar_info({
                                        car_id: "",
                                        owner_id: "",
                                        owner_name: "",
                                        gender: "",
                                        phone_no: "",
                                        plate_no: "",
                                        car_model: "",
                                        km: "",
                                    });
                                    setCar_id("");
                                    setCarStatus([
                                        ["message", "NOT REGISTERD"],
                                    ]);
                                    setCarStatus([
                                        ["message", "car not found"],
                                    ]);
                                }
                            } else {
                                // car not found
                                setCar_info({
                                    car_id: "",
                                    owner_id: "",
                                    owner_name: "",
                                    gender: "",
                                    phone_no: "",
                                    plate_no: "",
                                    car_model: "",
                                    km: "",
                                });
                                setCar_id("");
                                setCarStatus([["message", "NOT REGISTERD"]]);
                                setCarStatus([["message", "non"]]);
                                // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                            }
                        })
                        .catch((err) => {
                            console.log("error5", err);
                            setCar_id("");

                            displaySelected("", "", "CAR");
                            setCarStatus([["message", "Car not found"]]);
                        })
                        .finally(() => {
                            setclicked(false);
                        });
                    // } catch (err) {
                    //     // console.log(data);
                    //     console.log(err);
                    //     setCarStatus([["message", "non"]]);
                    //     setclicked(0);
                    // }
                }
            } else {
                if (car_id) {
                    try {
                        const url = `${Url}/edit/car`;
                        // const { data } = await
                        axios
                            .post(url, {
                                car_id: car_id,
                                car_model: car_model,
                                plate_no: plate_no,
                                km: km,
                                owner_id,
                            })
                            .then((res) => {
                                const { data } = res;
                                if (data.affectedRows > 0) {
                                    setCarStatus([["message", data.info]]);
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setCarStatus([
                                        ["message", data.sqlMessage],
                                    ]);
                                }
                            })
                            .catch((err) => {
                                console.log(err);
                            })
                            .finally(() => {
                                displaySelected(car_id, "", "CAR");
                                setclicked(0);
                            });
                    } catch (err) {
                        // console.log(data);
                        setclicked(0);
                        console.log(err);
                        setCarStatus([["message", "non"]]);
                    }
                } else {
                    try {
                        const url = `${Url}/new/car`;
                        // const { data } = await
                        axios
                            .post(url, {
                                plate_no: plate_no,
                                car_model: car_model,
                                km: km,
                                owner_id,
                            })
                            .then((res) => {
                                const { data } = res;
                                if (data.insertId > 0) {
                                    setCarStatus([["message", "non"]]);
                                    setCar_id(data.insertId);
                                } else {
                                    // setMessage("error", "/n", "Job ID : ", `${data.insertid}`);
                                    setCarStatus([
                                        ["message", data.sqlMessage],
                                    ]);
                                }
                            })
                            .catch((err) => {
                                console.log(err);
                            })
                            .finally(() => {
                                setclicked(0);
                            });
                    } catch (err) {
                        console.log(err);
                        setclicked(0);
                    }
                }
            }
        }
    };
    const clearAll = (what) => {
        if (what === "owner") {
            setOwner_info([
                {
                    car_id: "",
                    owner_id: "",
                    owner_name: "",
                    gender: "",
                    phone_no: "",
                    plate_no: "",
                    car_model: "",
                    km: "",
                },
            ]);
            setOwner_id("");
            setOwner_name("");
            setGender("");
            setPhone_no("");
        }
        if (what === "car") {
            setCar_info([
                {
                    car_id: "",
                    owner_id: "",
                    owner_name: "",
                    gender: "",
                    phone_no: "",
                    plate_no: "",
                    car_model: "",
                    km: "",
                },
            ]);
            // setOwner_id(data[0].owner_id);
            // setOwner_name(data[0].owner_name);
            // setGender(data[0].gender);
            // setPhone_no(data[0].phone_no);
            setCar_id("");
            setPlate_no("");
            setCar_model("");
            setKm("");
        }
    };
    const uploadID = (id, idof) => {
        props.onidchange(id, idof);
    };
    // const uploadIDToJob = () => {
    //     props.onidchange(id, idof);
    //     // props.id = car_id;
    //     // props.idof = "CAR";
    // };
    return (
        <div id="home">
            <section id="sidebar">
                <div className="mobileButton2">
                    <div className="paddingT1">
                        <div className="inline">
                            <span
                                className={
                                    sidebar ? "findType" : "findTypeActive"
                                }
                                onClick={() => {
                                    sidebar ? setsidebar(0) : setsidebar(1);
                                }}
                            >
                                <b>Find Customer !</b>
                            </span>
                        </div>
                    </div>
                </div>
                <div className={sidebar ? "-" : "sidebaroff"}>
                    <div className="mobileContainer">
                        <div className="stickyTop">
                            <SimpleSearch
                                myToken={props.myToken}
                                onidchange={uploadID}
                                selected={displaySelected}
                                link={"/main/home/new/customer"}
                            />
                        </div>
                        <div
                            className="exitmobile"
                            onClick={() => {
                                setsidebar(0);
                            }}
                        ></div>
                    </div>
                </div>
            </section>
            <section id="workspace">
                <>
                    <section>
                        <img
                            onClick={() => {
                                clearAll("owner");
                            }}
                            className="icon-svg-exit"
                            src={"/img/close2.svg?v=1"}
                            alt="call"
                        />
                        <div className="jobstatus">
                            <legend>
                                <span>
                                    <b>Owner Status</b>
                                </span>
                            </legend>
                            <div>
                                <div
                                    style={{
                                        display: "block",
                                        marginRight: "5px",
                                        marginTop: "5px",
                                        paddingLeft: "3px",
                                        paddingRight: "3px",
                                    }}
                                >
                                    <b>Status</b>
                                    <span> : </span>
                                    <span className={owner_id ? "green" : "-"}>
                                        {owner_id
                                            ? "REGISTERED"
                                            : "NOT REGISTERED"}
                                    </span>
                                </div>
                                {Array.isArray(userStatus)
                                    ? userStatus.map((val, ind) => (
                                          <div
                                              key={ind}
                                              style={{
                                                  marginRight: "10px",
                                                  marginTop: "5px",
                                                  paddingLeft: "5px",
                                                  paddingRight: "5px",
                                                  maxWidth: "16em",
                                              }}
                                          >
                                              <b>{val[0]}</b>
                                              <span> : </span>
                                              <span
                                                  className={
                                                      val[1] === "REGISTERED"
                                                          ? "green"
                                                          : "red"
                                                  }
                                              >
                                                  {val[1]}
                                              </span>
                                          </div>
                                      ))
                                    : null}
                            </div>
                        </div>
                        <form className="inline" onSubmit={handleSubmitUser}>
                            <section>
                                <div>
                                    <legend>
                                        <b>Owner info</b>
                                    </legend>
                                    <label> Owner ID:</label>
                                    <br />
                                    <input
                                        className="width4"
                                        type="number"
                                        value={owner_id}
                                        readOnly
                                    />
                                    <br />
                                    <label>Owner Name:</label>
                                    <br />
                                    <input
                                        type="text"
                                        value={owner_name}
                                        onChange={(e) => {
                                            setOwner_name(e.target.value);
                                        }}
                                        placeholder="owner name"
                                    />
                                    <br />
                                    <label>Gender :</label>
                                    <br />
                                    <select
                                        style={{ height: "2em" }}
                                        name="gender"
                                        id="gender"
                                        value={gender}
                                        onChange={(event) => {
                                            setGender(event.target.value);
                                        }}
                                    >
                                        <option value=""></option>
                                        <option value="M">male</option>
                                        <option value="F">female</option>
                                    </select>
                                    <br />
                                    <label>phone No.:</label>
                                    <br />
                                    <input
                                        type="text"
                                        value={phone_no}
                                        onChange={(e) => {
                                            setPhone_no(e.target.value);
                                        }}
                                        pattern="[0-9]{9}"
                                        placeholder="911223344"
                                        required
                                    />
                                    <a href={`tel:+251${phone_no}`}>
                                        <img
                                            className="b-image"
                                            src="/img/phone.png"
                                            alt="call"
                                        />
                                    </a>
                                    <br />
                                    <div className="grid2">
                                        <button
                                            type="submit"
                                            className="svg"
                                            value={"check"}
                                            // value={
                                            //     jobID ? "× Update job ×" : "+ Add Job +"
                                            // }
                                            disabled={clicked}
                                        >
                                            <img
                                                className="icon-svg icon-svg2"
                                                src="/img/check.svg?v=1"
                                                alt="call"
                                            />
                                        </button>
                                        {/* <input
                                            type="submit"
                                            
                                            disabled={clicked}
                                        /> */}
                                        <button
                                            type="submit"
                                            className="svg"
                                            // value={
                                            //     jobID ? "× Update job ×" : "+ Add Job +"
                                            // }
                                            disabled={clicked}
                                        >
                                            <img
                                                className="icon-svg"
                                                src={
                                                    owner_id
                                                        ? "/img/update.svg?v=1"
                                                        : "/img/add3.svg?v=1"
                                                }
                                                alt="call"
                                            />
                                        </button>
                                        {/* <input
                                            type="submit"
                                            value={owner_id ? "Update" : "ADD"}
                                            disabled={clicked}
                                        /> */}
                                    </div>
                                </div>
                            </section>
                        </form>
                        <section
                            style={{
                                minWidth: "8em",
                            }}
                        >
                            <legend>CarS</legend>
                            {/* <div> */}
                            <div
                                style={{
                                    maxHeight: "14em",
                                    overflowY: "scroll",
                                }}
                            >
                                {Array.isArray(owner_info)
                                    ? owner_info.map((owner, ind) => (
                                          <div
                                              className="resultList pointer"
                                              key={ind}
                                              onClick={() => {
                                                  displaySelected(
                                                      owner?.car_id,
                                                      owner?.owner_id,
                                                      "CAR"
                                                  );
                                              }}
                                          >
                                              {owner.car_id
                                                  ? `${owner.car_id} . ${owner.plate_no}`
                                                  : "NO CAR"}
                                          </div>
                                      ))
                                    : null}
                            </div>
                        </section>
                    </section>
                    <section>
                        <img
                            onClick={() => {
                                clearAll("car");
                            }}
                            className="icon-svg-exit"
                            src={"/img/close2.svg?v=1"}
                            alt="call"
                        />
                        <div className="jobstatus">
                            <legend>
                                <b>Car Status</b>
                            </legend>
                            <div>
                                <div
                                    style={{
                                        display: "block",
                                        marginRight: "5px",
                                        marginTop: "5px",
                                        paddingLeft: "3px",
                                        paddingRight: "3px",
                                    }}
                                >
                                    <b>Status</b>
                                    <span> : </span>
                                    <span className={car_id ? "green" : "-"}>
                                        {car_id
                                            ? "REGISTERED"
                                            : "NOT REGISTERED"}
                                    </span>
                                </div>
                                {Array.isArray(carStatus)
                                    ? carStatus.map((val, ind) => (
                                          <div
                                              key={ind}
                                              style={{
                                                  marginRight: "10px",
                                                  marginTop: "5px",
                                                  paddingLeft: "5px",
                                                  paddingRight: "5px",
                                                  maxWidth: "16em",
                                              }}
                                          >
                                              <b>{val[0]}</b>
                                              <span> : </span>
                                              <span
                                                  className={
                                                      val[1] === "REGISTERED"
                                                          ? "green"
                                                          : "red"
                                                  }
                                              >
                                                  {val[1]}
                                              </span>
                                          </div>
                                      ))
                                    : null}
                            </div>
                        </div>
                        <div
                            style={{
                                display: "inline-block",
                                verticalAlign: " text-top",
                            }}
                        >
                            <section style={{ minWidth: "8em" }}>
                                <legend>Owner</legend>
                                <div>
                                    <div style={{ display: "block" }}>
                                        <div
                                            className="resultList pointer"
                                            onClick={() => {
                                                displaySelected(
                                                    car_info[0]?.car_id,
                                                    car_info[0]?.owner_id,
                                                    "OWNER"
                                                );
                                            }}
                                        >
                                            {car_info[0]?.owner_id
                                                ? `${car_info[0]?.owner_id} . ${car_info[0]?.owner_name}`
                                                : "Not assigned"}
                                        </div>
                                    </div>
                                </div>
                            </section>
                            <form onSubmit={handleSubmit}>
                                <section>
                                    {/* <input
                                type="submit"
                                value={"> LINK <"}
                                disabled={clicked || !owner_id || !car_id}
                            /> */}
                                    <span
                                        style={{
                                            border: "solid black 0.5px",
                                            paddingLeft: "5px",
                                            paddingRight: "5px",
                                        }}
                                    >
                                        <b>Status</b>
                                        <span> : </span>
                                        <span
                                            className={
                                                car_info[0]?.owner_id ==
                                                owner_id
                                                    ? owner_id
                                                        ? "green"
                                                        : "-"
                                                    : "-"
                                            }
                                        >
                                            {car_info[0]?.owner_id == owner_id
                                                ? owner_id
                                                    ? "LINKED"
                                                    : "NOT LINKED"
                                                : "NOT LINKED"}
                                        </span>
                                    </span>
                                    {/* </section>
                            <section> */}
                                    <br />
                                    <Link
                                        to={"/main/home/new/job"}
                                        onClick={() => uploadID(car_id, "CAR")}
                                    >
                                        <button
                                            className="button1"
                                            type="submit"
                                            // value={""}
                                            disabled={!car_id}
                                        >
                                            +new_job+
                                        </button>
                                    </Link>
                                </section>
                                <br />
                            </form>
                        </div>
                        <form className="inline" onSubmit={handleSubmitCar}>
                            <section>
                                <div>
                                    <legend>
                                        <b> Car info </b>
                                    </legend>
                                    <label> Car ID:</label>
                                    <br />
                                    <input
                                        className="width4"
                                        type="number"
                                        value={car_id}
                                        readOnly
                                    />
                                    <br />
                                    <label>plate No.:</label>
                                    <br />
                                    <input
                                        type="text"
                                        value={plate_no}
                                        placeholder="A12345"
                                        onChange={(e) => {
                                            setPlate_no(e.target.value);
                                        }}
                                        required
                                        // style="text-transform:uppercase"
                                        // pattern="[A-Z]?[0-9]{5}"
                                    />
                                    <br />
                                    <label>car model:</label>
                                    <br />
                                    <input
                                        autoComplete="on"
                                        type="text"
                                        value={car_model}
                                        placeholder="hilux"
                                        onChange={(e) => {
                                            setCar_model(e.target.value);
                                        }}
                                    />
                                    <br /> <label>Traveled (Km):</label>
                                    <br />
                                    <input
                                        type="number"
                                        value={km}
                                        required
                                        placeholder="km"
                                        onChange={(e) => {
                                            setKm(e.target.value);
                                        }}
                                    />
                                    <br />
                                    <div className="grid2">
                                        <button
                                            type="submit"
                                            className="svg"
                                            value={"check"}
                                            // value={
                                            //     jobID ? "× Update job ×" : "+ Add Job +"
                                            // }
                                            disabled={clicked}
                                        >
                                            <img
                                                className="icon-svg icon-svg2"
                                                src="/img/check.svg?v=1"
                                                alt="call"
                                            />
                                        </button>
                                        {/* <input
                                            type="submit"
                                            value={"check"}
                                            disabled={clicked}
                                        /> */}
                                        <button
                                            type="submit"
                                            className="svg"
                                            value={"add"}
                                            // value={
                                            //     jobID ? "× Update job ×" : "+ Add Job +"
                                            // }
                                            disabled={clicked}
                                        >
                                            <img
                                                className="icon-svg"
                                                src={
                                                    car_id
                                                        ? owner_id
                                                            ? "/img/link.svg?v=1"
                                                            : "/img/update.svg?v=1"
                                                        : "/img/add3.svg?v=1"
                                                }
                                                alt="call"
                                            />
                                        </button>
                                        {/* <input
                                            type="submit"
                                            value={car_id ? "Update" : "ADD"}
                                            disabled={clicked}
                                        /> */}
                                    </div>
                                </div>
                            </section>
                        </form>
                    </section>
                </>
            </section>
            <section id="latestbar">
                <div className="stickyTop">
                    <div className="paddingT1">
                        <p>
                            <b>cars and owners</b>
                        </p>
                    </div>
                    {/* <img src="./tiny_refresh.png" alt="rr" sizes="" srcset="" /> */}
                    <div>
                        <table
                            style={{
                                backgroundColor: "rgba(60,220,200,0.9)",
                                fontWeight: "bold",
                            }}
                        >
                            <thead>
                                <tr>
                                    <td>Car ID</td>
                                    <td>Plate No</td>
                                    <td>Phone No</td>
                                    <td>Car Model</td>
                                </tr>
                            </thead>
                            <tbody>
                                {Array.isArray(latestCars) ? (
                                    latestCars.map((cars, ind) => (
                                        <tr
                                            key={ind}
                                            className={
                                                ind % 2 === 0
                                                    ? "darkRow pointer"
                                                    : "pointer"
                                            }
                                            onClick={() =>
                                                displaySelected(
                                                    cars.car_id,
                                                    cars.car_id,
                                                    "CAR"
                                                )
                                            }
                                        >
                                            <td>{cars.car_id}</td>
                                            <td>{cars.plate_no}</td>
                                            <td>{cars.phone_no}</td>
                                            <td>{cars.car_model}</td>
                                        </tr>
                                    ))
                                ) : (
                                    <tr>
                                        <td>Car ID</td>
                                        <td>Plate No</td>
                                        <td>Phone No</td>
                                        <td>Car Model</td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                    </div>
                </div>
            </section>
        </div>
    );
}
