import { useState } from "react";
import { Link, Routes, Route } from "react-router-dom";

import "./SamplePage.css";

function WebHeader(props) {
    // const setClass = (ccc) => {};
    // console.log(props.startNav);
    const nav = [
        [0, "Home", "/main/home"],
        [1, "List", "/main/list"],
        [2, "Report", "/main/report"],
        // [2, "jobs", "/main/jobs"],
        // [3, "customers", "/main/customers"],
        // [4, "companies", "/main"],
    ];
    const nav2 = [
        [
            [0, " JOBS ", "/main/home/new/job"],
            [1, "CUSTOMERS", "/main/home/new/customer"],
        ],
        [
            [0, " TODAY", "/main/list/today"],
            [1, "THIS WEEK", "/main/list/thisweek"],
            [2, "UNPAID", "/main/list/unpaid"],
            [3, "ALL CUSTOMERS", "/main/list/allCustomers"],
            [4, "ALL JOBS", "/main/list/allJobs"],
        ],
        [
            [0, "TODAY", "/main/report/today"],
            [1, "THIS WEEK", "/main/report/thisweek"],
            [2, "DATE RANGE", "/main/report/daterange"],
        ],
    ];
    const [current, setCurrent] = useState(0);
    const [current1, setCurrent1] = useState(0);
    const [current2, setCurrent2] = useState(0);
    const [current3, setCurrent3] = useState(0);
    const navSelected = (num) => {
        setCurrent(num);
    };
    // console.log(props);
    const nav2Selected = (num) => {
        if (current === 0) {
            setCurrent1(num);
            // console.log(num);
        }
        if (current === 1) {
            setCurrent2(num);
            // console.log(num);
        }
        if (current === 2) {
            setCurrent3(num);
        }
    };
    const Navigation2 = (props) => {
        return (
            <ul className={"headerulll"}>
                {nav2[props.curentt].map((val) => (
                    <li
                        key={val[0]}
                        id={props.curent22 === val[0] ? "active2" : "-"}
                    >
                        <Link
                            to={val[2]}
                            onClick={() => {
                                nav2Selected(val[0]);
                                navSelected(props.curentt);
                            }}
                        >
                            {val[1]}
                        </Link>
                    </li>
                ))}
            </ul>
        );
    };

    return (
        <>
            <div className="headerul">
                <div className="garage pointer">
                    <Link
                        style={{ textDecoration: "none", color: "inherit" }}
                        to="/main/home/new/job"
                    >
                        <span>W</span>
                        <p>Wondwosen</p>
                        <p>garage</p>
                    </Link>
                </div>
                <Routes>
                    <Route
                        path="/"
                        element={
                            props.authorized ? (
                                <ul className="headerull">
                                    <li>
                                        <Link
                                            to={nav2[0][current1][2]}
                                            onClick={() => {
                                                navSelected(0);
                                            }}
                                            id={"-"}
                                            className="pointer"
                                        >
                                            {nav[0][1]}
                                        </Link>
                                    </li>
                                    <li>
                                        <Link
                                            to={nav2[1][current2][2]}
                                            onClick={() => {
                                                navSelected(1);
                                            }}
                                            id={"-"}
                                            className="pointer"
                                        >
                                            {nav[1][1]}
                                        </Link>
                                    </li>
                                    <li>
                                        <Link
                                            to={nav2[2][current3][2]}
                                            onClick={() => {
                                                navSelected(2);
                                            }}
                                            id={"-"}
                                            className="pointer"
                                        >
                                            {nav[2][1]}
                                        </Link>
                                    </li>
                                </ul>
                            ) : (
                                <p style={{ margin: "0px", color: "red" }}>
                                    Please Login to access Wondwosen garage DB!
                                </p>
                            )
                        }
                    />
                    <Route
                        path="/main/home/*"
                        element={
                            <ul className="headerull">
                                <li>
                                    <Link
                                        to={nav2[0][current1][2]}
                                        onClick={() => {
                                            navSelected(0);
                                        }}
                                        id={"active"}
                                        className="pointer"
                                    >
                                        {nav[0][1]}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to={nav2[1][current2][2]}
                                        onClick={() => {
                                            navSelected(1);
                                        }}
                                        id={"-"}
                                        className="pointer"
                                    >
                                        {nav[1][1]}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to={nav2[2][current3][2]}
                                        onClick={() => {
                                            navSelected(2);
                                        }}
                                        id={"-"}
                                        className="pointer"
                                    >
                                        {nav[2][1]}
                                    </Link>
                                </li>
                            </ul>
                        }
                    />
                    <Route
                        path="/main/list/*"
                        element={
                            <ul className="headerull">
                                <li>
                                    <Link
                                        to={nav2[0][current1][2]}
                                        onClick={() => {
                                            navSelected(0);
                                        }}
                                        id={"-"}
                                        className="pointer"
                                    >
                                        {nav[0][1]}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to={nav2[1][current2][2]}
                                        onClick={() => {
                                            navSelected(1);
                                        }}
                                        id={"active"}
                                        className="pointer"
                                    >
                                        {nav[1][1]}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to={nav2[2][current3][2]}
                                        onClick={() => {
                                            navSelected(2);
                                        }}
                                        id={"-"}
                                        className="pointer"
                                    >
                                        {nav[2][1]}
                                    </Link>
                                </li>
                            </ul>
                        }
                    />
                    <Route
                        path="/main/report/*"
                        element={
                            <ul className="headerull">
                                <li>
                                    <Link
                                        to={nav2[0][current1][2]}
                                        onClick={() => {
                                            navSelected(0);
                                        }}
                                        id={"-"}
                                        className="pointer"
                                    >
                                        {nav[0][1]}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to={nav2[1][current2][2]}
                                        onClick={() => {
                                            navSelected(1);
                                        }}
                                        id={"-"}
                                        className="pointer"
                                    >
                                        {nav[1][1]}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to={nav2[2][current3][2]}
                                        onClick={() => {
                                            navSelected(2);
                                        }}
                                        id={"active"}
                                        className="pointer"
                                    >
                                        {nav[2][1]}
                                    </Link>
                                </li>
                            </ul>
                        }
                    />
                    <Route
                        path="/login"
                        element={
                            props.authorized ? (
                                <ul className="headerull">
                                    <li>
                                        <Link
                                            to={nav2[0][current1][2]}
                                            onClick={() => {
                                                navSelected(0);
                                            }}
                                            id={"-"}
                                            className="pointer"
                                        >
                                            {nav[0][1]}
                                        </Link>
                                    </li>
                                    <li>
                                        <Link
                                            to={nav2[1][current2][2]}
                                            onClick={() => {
                                                navSelected(1);
                                            }}
                                            id={"-"}
                                            className="pointer"
                                        >
                                            {nav[1][1]}
                                        </Link>
                                    </li>
                                    <li>
                                        <Link
                                            to={nav2[2][current3][2]}
                                            onClick={() => {
                                                navSelected(2);
                                            }}
                                            id={"-"}
                                            className="pointer"
                                        >
                                            {nav[2][1]}
                                        </Link>
                                    </li>
                                </ul>
                            ) : (
                                <p style={{ margin: "0px", color: "red" }}>
                                    Please Login to access Wondwosen garage DB!
                                </p>
                            )
                        }
                    />
                </Routes>
                <div className="garage pointer">
                    <Link
                        style={{ textDecoration: "none", color: "inherit" }}
                        to="/login"
                    >
                        <span>U</span>
                        <>
                            {props.authorized ? <p>user</p> : <p>Login</p>}
                            <p>{props.name}</p>
                        </>
                    </Link>
                </div>
                <Routes>
                    <Route
                        path="/login"
                        element={
                            <p style={{ margin: "0px", color: "red" }}>
                                - - - - - - - - - - - -
                            </p>
                        }
                    />
                    <Route
                        path="/"
                        element={
                            <p style={{ margin: "0px", color: "red" }}>
                                - - - - - - - - - - - -
                            </p>
                        }
                    />
                    <Route
                        path="/main/home/*"
                        element={
                            <>
                                <Routes>
                                    <Route
                                        path="/new/job"
                                        element={
                                            <Navigation2
                                                curentt={0}
                                                curent22={0}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/new/customer"
                                        element={
                                            <Navigation2
                                                curentt={0}
                                                curent22={1}
                                            />
                                        }
                                    />
                                </Routes>
                            </>
                        }
                    />
                    <Route
                        path="/main/list/*"
                        element={
                            <>
                                <Routes>
                                    <Route
                                        path="/today"
                                        element={
                                            <Navigation2
                                                curentt={1}
                                                curent22={0}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/thisweek"
                                        element={
                                            <Navigation2
                                                curentt={1}
                                                curent22={1}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/unpaid"
                                        element={
                                            <Navigation2
                                                curentt={1}
                                                curent22={2}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/allCustomers"
                                        element={
                                            <Navigation2
                                                curentt={1}
                                                curent22={3}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/allJobs"
                                        element={
                                            <Navigation2
                                                curentt={1}
                                                curent22={4}
                                            />
                                        }
                                    />
                                </Routes>
                            </>
                        }
                    />
                    <Route
                        path="/main/report/*"
                        exact
                        element={
                            <>
                                <Routes>
                                    <Route
                                        path="/today"
                                        element={
                                            <Navigation2
                                                curentt={2}
                                                curent22={0}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/thisweek"
                                        element={
                                            <Navigation2
                                                curentt={2}
                                                curent22={1}
                                            />
                                        }
                                    />
                                    <Route
                                        path="/daterange"
                                        element={
                                            <Navigation2
                                                curentt={2}
                                                curent22={2}
                                            />
                                        }
                                    />
                                </Routes>
                            </>
                        }
                    />
                </Routes>
            </div>
        </>
    );
}
export default WebHeader;
