import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import NumberFormat from "react-number-format";

import "./Costomer.css";
import "../SamplePage.css";
// import { Url } from "../../url";
// import axios from "axios";
// import SingleCostomer from "./SingleCostomers";
// import TableHeader from "./TableHeader";
// import TableFooter from "./Tablefooter";

// import costomers from "./customerSample";

function ListTableDateRange(props) {
    const navigate = useNavigate();
    const Url = props.myToken;
    const cellHeader =
        props.head === "customers"
            ? [
                  "ID",
                  "name",
                  "gender",
                  "phone No",
                  "plate No",
                  "car model",
                  "km",
              ]
            : [
                  "job ID",
                  "Car ID",
                  "Plate No",
                  "km",
                  "recieved date",
                  "type",
                  "p_cash",
                  "p_cheque",
                  "p_transfer",
                  "p_credit",
                  "replaced parts",
                  "delivered date",
                  "technician",
                  "note",
              ];
    const [tableV, setTableV] = useState([
        {
            1: "",
            2: "Enter",
            3: "",
            4: "a Valid date range",
        },
        {},
    ]);
    const [relod, setRelod] = useState(0);
    const [date1, setdate1] = useState("");
    const [date2, setdate2] = useState("");
    const [p_cash, setp_cash] = useState(0);
    const [p_cheque, setp_cheque] = useState(0);
    const [p_transfer, setp_transfer] = useState(0);
    const [p_credit, setp_credit] = useState(0);
    const [p_total, setp_total] = useState(0);
    // useEffect(() => {
    const dateRangeReport = () => {
        setRelod(1);

        try {
            if (date1) {
                axios
                    .get(`${Url}/report/daterange/${date1}to${date2}`)
                    .then((response) => {
                        console.log(response);
                        if (Array.isArray(response.data)) {
                            if (response.data.length > 1) {
                                let rr = response.data;
                                let total = rr.pop();
                                // console.log(total);
                                setp_cash(total.p_cash);
                                setp_cheque(total.p_cheque);
                                setp_credit(total.p_credit);
                                setp_transfer(total.p_transfer);
                                setp_total(
                                    Number(total.p_cash) +
                                        Number(total.p_cheque) +
                                        Number(total.p_transfer) -
                                        Number(total.p_credit)
                                );
                                setTableV(rr);
                            } else {
                                setp_cash(0);
                                setp_cheque(0);
                                setp_credit(0);
                                setp_transfer(0);
                                setp_total(0);
                                setTableV([
                                    ["NO ENTRIES "],
                                    ["FOUND FOR THIS TIME RANGE !!!"],
                                ]);
                            }
                        } else {
                            setp_cash(0);
                            setp_cheque(0);
                            setp_credit(0);
                            setp_transfer(0);
                            setp_total(0);
                            setTableV([
                                ["NO ENTRIES "],
                                ["FOUND FOR THIS TIME RANGE !!!"],
                            ]);
                        }
                        // setTableV(response.data);
                        // console.log(response);
                        // console.log(response.data);
                    })
                    .catch((error) => {
                        // console.log("this is the error !");
                        // console.log(error.response.status);
                        console.log(error);
                        if (error.response.status == "401") {
                            //window.location.reload(false);
                            navigate("/login");
                            // window.location = "/login";
                        }
                    })
                    .finally(() => {
                        setRelod(0);
                    });
            }
            // console.log(cellValue);
        } catch (err) {
            console.log(err);
        }
    };
    // console.log("effect");
    const uploadID = (id, idof) => {
        // console.log(id, idof);
        // console.log(props);
        props.onidchange(id, idof);
    };

    return (
        <div>
            <div id="workspace">
                <section style={{ margin: "0px", padding: "0px" }}>
                    <form
                        style={{ display: "inline-block" }}
                        onSubmit={(e) => {
                            e.preventDefault();
                            dateRangeReport();
                        }}
                    >
                        &nbsp; &nbsp;
                        <span>
                            <b>{" FROM  "}</b>&nbsp; &nbsp;
                        </span>
                        <input
                            type="date"
                            required
                            name="date1"
                            value={date1}
                            onChange={(e) => {
                                setdate1(e.target.value);
                            }}
                        />
                        &nbsp; &nbsp; &nbsp; &nbsp;
                        <span>
                            <b>{" TO  "}</b>
                            &nbsp; &nbsp;
                        </span>
                        <input
                            type="date"
                            required
                            name="date2"
                            value={date2}
                            onChange={(e) => {
                                setdate2(e.target.value);
                            }}
                        />
                        <input type="submit" />
                    </form>
                    <h2
                        style={{
                            margin: "0px",
                            display: "inline-block",
                            padding: "2px 3em",
                        }}
                    >
                        {props.head}
                    </h2>
                    <form className="total" style={{ display: "inline-block" }}>
                        &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" CASH :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_cash ? p_cash : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" CHEQUE :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_cheque ? p_cheque : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" TRANSFER :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_transfer ? p_transfer : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" CREDIT :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_credit ? p_credit : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" TOTAL :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_total ? p_total : "0"}
                                readOnly
                            />
                        </div>
                    </form>
                </section>
            </div>
            <div className="allCostomers">
                <table className="table1">
                    <thead>
                        <tr className="thead">
                            <td>-</td>
                            {Array.isArray(cellHeader)
                                ? cellHeader.map((val, ind) => (
                                      <td key={ind}>{val}</td>
                                  ))
                                : null}
                        </tr>
                    </thead>
                    <tbody>
                        {Array.isArray(tableV)
                            ? tableV.map((val, ind) => (
                                  <tr
                                      key={ind}
                                      className={ind % 2 === 0 ? "darkRow" : ""}
                                  >
                                      <td
                                          onClick={() => {
                                              uploadID(val.job_id, "jobs");
                                          }}
                                      >
                                          <div id="edit">
                                              <Link to={"/main/home/new/job"}>
                                                  --
                                              </Link>
                                          </div>
                                      </td>
                                      {Object.keys(val).map((key, ind) => (
                                          <td key={ind}>{val[key]} </td>
                                      ))}
                                  </tr>
                              ))
                            : null}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default ListTableDateRange;
