import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";

import "./Costomer.css";
// import { Url } from "../../url";
// import axios from "axios";
// import SingleCostomer from "./SingleCostomers";
// import TableHeader from "./TableHeader";
// import TableFooter from "./Tablefooter";

// import costomers from "./customerSample";

function ListTable(props) {
    const navigate = useNavigate();
    const [Url, setUrl] = useState(props.myToken);
    const cellHeader =
        props.head === "customers"
            ? [
                  "ID",
                  "name",
                  "gender",
                  "phone No",
                  "plate No",
                  "car model",
                  "km",
              ]
            : [
                  "job ID",
                  "Car ID",
                  "km",
                  "recieved date",
                  "type",
                  "p_cash",
                  "p_cheque",
                  "p_transfer",
                  "p_credit",
                  "replaced parts",
                  "delivered date",
                  "technician",
                  "note",
              ];
    const [tableV, setTableV] = useState([
        {
            1: "",
            2: "Server",
            3: "",
            4: "unavailable",
        },
        {},
    ]);
    useEffect(() => {
        // console.log("effect");
        // console.log(props.head);
        try {
            axios
                .get(`${Url}/${props.link}`)
                .then((response) => {
                    if (response.data.length > 1) {
                        setTableV(response.data);
                    } else {
                        setTableV([
                            ["NO ENTRIES "],
                            ["FOUND FOR THIS TIME RANGE !!!"],
                        ]);
                    }
                })
                .catch((error) => {
                    // console.log("this is the error !");
                    // console.log(error.response.status);
                    if (error.response.status == "401") {
                        //window.location.reload(false);
                        navigate("/login");
                        // window.location = "/login";
                    }
                });
            // console.log(cellValue);
        } catch (err) {
            console.log(err);
        }
    }, []);
    const uploadID = (id, idof) => {
        props.onidchange(id, props.head == "jobs" ? props.head : "CAR");
    };

    return (
        <div className="allCostomers">
            <h1>{props.head}</h1>
            <table className="table1">
                <thead>
                    <tr className="thead">
                        <td>-</td>
                        {Array.isArray(cellHeader)
                            ? cellHeader.map((val, ind) => (
                                  <td key={ind}>{val}</td>
                              ))
                            : null}
                    </tr>
                </thead>
                <tbody>
                    {Array.isArray(tableV)
                        ? tableV.map((val, ind) => (
                              <tr
                                  key={ind}
                                  className={
                                      ind === 20
                                          ? "darkRow"
                                          : ind % 2 === 0
                                          ? "darkRow"
                                          : ""
                                  }
                              >
                                  <td
                                      onClick={() => {
                                          props.head == "jobs"
                                              ? uploadID(val.job_id, props.head)
                                              : uploadID(
                                                    val.car_id,
                                                    props.head
                                                );
                                      }}
                                  >
                                      <div id="edit">
                                          <Link
                                              to={
                                                  props.head == "jobs"
                                                      ? "/main/home/new/job"
                                                      : "/main/home/new/customer"
                                              }
                                          >
                                              --
                                          </Link>
                                      </div>
                                  </td>
                                  {Object.keys(val).map((key, ind) => (
                                      <td key={ind}>{val[key]} </td>
                                  ))}
                              </tr>
                          ))
                        : null}
                </tbody>
            </table>
        </div>
    );
}

export default ListTable;
