import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import NumberFormat from "react-number-format";

import "./Costomer.css";
// import { Url } from "../../url";
// import SingleCostomer from "./SingleCostomers";
// import TableHeader from "./TableHeader";
// import TableFooter from "./Tablefooter";

// import costomers from "./customerSample";

function ReportTable(props) {
    const navigate = useNavigate();
    const [Url, setUrl] = useState(props.myToken);
    const cellHeader =
        props.head === "customers"
            ? [
                  "ID",
                  "name",
                  "gender",
                  "phone No",
                  "plate No",
                  "car model",
                  "km",
              ]
            : [
                  "job ID",
                  "Car ID",
                  "Plate No",
                  "km",
                  "recieved date",
                  "type",
                  "p_cash",
                  "p_cheque",
                  "p_transfer",
                  "p_credit",
                  "replaced parts",
                  "delivered date",
                  "technician",
                  "note",
              ];
    const [tableV, setTableV] = useState([
        {
            1: "",
            2: "Server",
            3: "",
            4: "unavailable",
        },
        {},
    ]);
    const [p_cash, setp_cash] = useState(0);
    const [p_cheque, setp_cheque] = useState(0);
    const [p_transfer, setp_transfer] = useState(0);
    const [p_credit, setp_credit] = useState(0);
    const [p_total, setp_total] = useState(0);
    useEffect(() => {
        // console.log("effect");
        // console.log(props.head);
        try {
            axios
                .get(`${Url}/${props.link}`)
                .then((response) => {
                    // console.log(response);
                    if (Array.isArray(response.data)) {
                        if (response.data.length > 1) {
                            let rr = response.data;
                            // console.log("rr", rr);
                            let total = rr.pop();
                            // console.log("total", total);
                            setp_cash(total.p_cash);
                            setp_cheque(total.p_cheque);
                            setp_credit(total.p_credit);
                            setp_transfer(total.p_transfer);
                            setp_total(
                                Number(total.p_cash) +
                                    Number(total.p_cheque) +
                                    Number(total.p_transfer) +
                                    Number(total.p_credit)
                            );
                            setTableV(rr);
                        } else {
                            setp_cash(0);
                            setp_cheque(0);
                            setp_credit(0);
                            setp_transfer(0);
                            setp_total(0);
                            setTableV([
                                ["NO ENTRIES "],
                                ["FOUND FOR THIS TIME RANGE !!!"],
                            ]);
                        }
                    } else {
                        setp_cash(0);
                        setp_cheque(0);
                        setp_credit(0);
                        setp_transfer(0);
                        setp_total(0);
                        setTableV([
                            ["NO ENTRIES "],
                            ["FOUND FOR THIS TIME RANGE !!!"],
                        ]);
                    }
                })
                .catch((error) => {
                    // console.log("this is the error !");
                    // console.log(error.response.status);
                    if (error.response.status == "401") {
                        //window.location.reload(false);
                        navigate("/login");
                        // window.location = "/login";
                    }
                });
            // console.log(cellValue);
        } catch (err) {
            console.log(err);
        }
    }, [props.head]);
    const uploadID = (id, idof) => {
        // console.log(id, idof);
        // console.log(props);
        props.onidchange(id, idof);
    };

    return (
        <div className="allCostomers">
            <div id="workspace">
                <section style={{ margin: "0px", padding: "0px" }}>
                    <div
                        style={{
                            textAlign: "center",
                        }}
                    >
                        <h2
                            style={{
                                margin: "0px",
                                padding: "5px 3em",
                                textAlign: "center",
                                display: "inline",
                            }}
                        >
                            {props.head}
                            &nbsp; &nbsp; &nbsp;
                        </h2>
                        <h3
                            style={{
                                margin: "0px",
                                padding: "5px 3em",
                                textAlign: "center",
                                display: "inline",
                            }}
                        >
                            {Date().slice(0, 21)}
                        </h3>
                    </div>
                    <form style={{ display: "inline-block" }} className="total">
                        <div className="inline">
                            &nbsp;
                            <span>
                                <b>{" CASH :"}</b>&nbsp;
                            </span>
                            {/* <NumberFormat
  value={this.state.profit}
  thousandSeparator={true}
  prefix={'$'}
  onValueChange={(values) => {
    const { formattedValue, value } = values;
    // formattedValue = $2,223
    // value ie, 2223
    this.setState({ profit: formattedValue });
  }}
/> */}
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_cash ? p_cash : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" CHEQUE :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_cheque ? p_cheque : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" TRANSFER :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_transfer ? p_transfer : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div className="inline">
                            <span>
                                <b>{" CREDIT :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_credit ? p_credit : "0"}
                                readOnly
                            />
                        </div>
                        &nbsp; &nbsp;
                        <div
                            style={{ backgroundColor: "rgba(255, 0, 0, 0.5)" }}
                            className="inline"
                        >
                            <span>
                                <b>{" TOTAL :"}</b>&nbsp;
                            </span>
                            <NumberFormat
                                thousandSeparator={true}
                                prefix={"birr "}
                                type="text"
                                required
                                value={p_total ? p_total : "0"}
                                readOnly
                            />
                        </div>
                    </form>
                </section>
            </div>
            <table className="table1">
                <thead>
                    <tr className="thead">
                        <td>-</td>
                        {Array.isArray(cellHeader)
                            ? cellHeader.map((val, ind) => (
                                  <td key={ind}>{val}</td>
                              ))
                            : null}
                    </tr>
                </thead>
                <tbody>
                    {Array.isArray(tableV)
                        ? tableV.map((val, ind) => (
                              <tr
                                  key={ind}
                                  className={
                                      (val.p_cash == "0") &
                                      (val.p_cheque == "0") &
                                      (val.p_credit == "0") &
                                      (val.p_transfer == "0")
                                          ? "badRow"
                                          : ind % 2 === 0
                                          ? "darkRow"
                                          : ""
                                  }
                              >
                                  <td
                                      onClick={() => {
                                          uploadID(val.job_id, "jobs");
                                      }}
                                  >
                                      <div id="edit">
                                          <Link to={"/main/home/new/job"}>
                                              --
                                          </Link>
                                      </div>
                                  </td>
                                  {Object.keys(val).map((key, ind) => (
                                      <td key={ind}>{val[key]} </td>
                                  ))}
                              </tr>
                          ))
                        : null}
                </tbody>
            </table>
        </div>
    );
}

export default ReportTable;
