    const [dispsearch, setdispsearch] = useState(1);
    const [dispjob, setdispjob] = useState(0);
    const [sidebar, setsidebar] = useState(0);
    const [latestlist, setlatestlist] = useState(1);
    const [latestCars, setLatestCars] = useState([""]);
