import axios from "axios";
import React, { useState, useEffect } from "react";
import "./Login.css";
// import { Url } from "./url";

export default function LogIn(props) {
    const [user, setUser] = useState("");
    const [password, setpassword] = useState("");
    const [err, seterr] = useState(0);
    const [err_m, seterr_m] = useState("");
    const [auth, setAuth] = useState(props.authorized); //
    const { authorized, onCheckAuthorized, myToken } = props;
    // console.log(props);
    useEffect(() => {
        // console.log("auth checked");
        let url = myToken;
        let name = "unknown";

        //         //   setUser(foundToken);
        if (myToken === "/token") {
            const storedToken = localStorage.getItem("storedToken");
            const storedName = localStorage.getItem("storedName");
            if (storedToken) {
                url = JSON.parse(storedToken);
                name = JSON.parse(storedName);
            }
        }
        axios
            .get(`${url}/authorize`)
            .then((res) => {
                const { data } = res;
                if (data?.length) {
                    // console.log("token ", data[0]?.token);
                    setAuth(true);
                    props.onCheckAuthorized(true);
                    props.setMyToken(`/${data[0].token}`);
                    props.setToken(data[0].token);
                    // props.onToken(data[0]?.token);
                    props.setName(name);
                    //////////////////////////////////
                    // localStorage.clear();
                    seterr(2);
                    seterr_m("Welcome !");
                }
            })
            .catch((err) => {
                onCheckAuthorized(false);
                setAuth(false);
                console.log("not authorized ");
            });
        //     checkAuth();
        // console.log("check auth");
        // const storedToken = localStorage.getItem("storedToken");
        // if (storedToken) {
        //     const foundToken = JSON.parse(storedToken);
        //     // console.log(foundToken);
        //     axios
        //         .get(`/${foundToken}/authorize`)
        //         .then((res) => {
        //             // console.log("auth message");
        //             // console.log(res);
        //             // seterr(2);
        //             // seterr_m("Welcome !");
        //             // localStorage.setItem("storedToken", JSON.stringify(res.token));
        //             // localStorage.setItem("storedName", JSON.stringify(res.name));
        //             const name = JSON.parse(localStorage.getItem("storedName"));
        //             props.setMyToken(res.token);
        //             props.setToken(res.token);
        //             props.setName(name);
        //             setAuth(true);

        //             onCheckAuthorized(true);
        //         })
        //         .catch((err) => {
        //             setAuth(false);
        //             props.onCheckAuthorized(false);
        //         });
        // } else {
        // }
    }, []);
    const onLogin = async (event) => {
        event.preventDefault();
        // console.log(user, password);
        // console.log(props.myToken);
        try {
            // console.log("login url ", props.myToken, "login");
            axios
                .post(`${props.myToken}/login`, {
                    user,
                    password,
                })
                .then((res) => {
                    // console.log("login res:", res);
                    const { data, status } = res;
                    if (status === 200) {
                        if (data?.length) {
                            // console.log("token ", data[0]?.token);

                            localStorage.setItem(
                                "storedToken",
                                JSON.stringify(data[0].token)
                            );
                            localStorage.setItem(
                                "storedName",
                                JSON.stringify(data[0].name)
                            );
                            setAuth(true);
                            props.onCheckAuthorized(true);
                            props.setMyToken(`/${data[0].token}`);
                            props.setToken(data[0].token);
                            // props.onToken(data[0]?.token);
                            props.setName(data[0]?.name);
                            //////////////////////////////////
                            // localStorage.clear();
                            seterr(2);
                            seterr_m("Welcome !");
                        } else {
                            // console.log(data);
                            seterr(1);
                            seterr_m("Incorrect name or password !");
                        }
                    } else {
                        seterr(1);
                        seterr_m("Error !");
                    }
                    // console.log(response);
                })
                .catch((err) => {
                    // console.log("unable to login");
                    seterr(1);
                    seterr_m("Error !");
                })
                .finally(() => {
                    // checkAuth();
                });
        } catch (err) {
            seterr(1);
            seterr_m("Error !");
            // console.log(err);
        }
    };
    // useEffect(() => {
    //     // console.log("token :", props.myToken);
    //     // console.log("login");
    // });
    const onLogout = async (event) => {
        event.preventDefault();
        // console.log(user, password);
        // console.log(props.myToken);
        try {
            // console.log("login url ", props.myToken, "login");
            axios
                .delete(`${props.myToken}/logout`, {
                    user,
                    password,
                })
                .then((res) => {
                    // console.log(res);
                    localStorage.clear();
                    setAuth(false);
                    props.onCheckAuthorized(false);
                    props.setMyToken("/token");
                    props.setToken("token");
                    // props.onToken("token");
                    props.setName("Unknown");

                    seterr(2);
                    seterr_m("GOODBYE !");

                    // console.log(response);
                })
                .catch((err) => {
                    setAuth(false);
                    props.onCheckAuthorized(false);
                    seterr(1);
                    // props.onCheckAuthorized(false);
                    props.setMyToken("/token");
                    props.setToken("token");
                    // props.onToken("token");
                    props.setName("Unknown");
                    seterr_m("Not authorized !");
                });
        } catch (err) {
            seterr(1);
            seterr_m("Login Error 2 !");
            props.setName("Unknown");
            // console.log(err);
            props.onCheckAuthorized(false);
            setAuth(false);
        }
    };
    // const checkAuth = () => {
    //     if (authorized) {
    //         const storedToken = localStorage.getItem("storedToken");
    //         if (storedToken) {
    //             const foundToken = JSON.parse(storedToken);
    //             //   setUser(foundToken);
    //             axios
    //                 .get(`/${foundToken}/authorize`)
    //                 .then((res) => {
    //                     console.log("auth message");
    //                     console.log(res);
    //                 })
    //                 .catch((err) => {
    //                     // props.onCheckAuthorized(false);
    //                     setAuth(false);
    //                     console.log("auth failed");
    //                 });
    //         }
    //     }
    // };
    // console.log(props.authorized);
    return (
        <div className="login">
            <div className="border">
                <div
                    className={
                        err === 2
                            ? "successmessage"
                            : err === 1
                            ? "errmessage"
                            : "off"
                    }
                >
                    <p
                        style={{
                            marginTop: "0px",
                            maxWidth: "fit-content",
                        }}
                    >
                        {err_m}
                    </p>
                </div>
                {auth ? (
                    <>
                        <h1 style={{ margine: "2px", padding: "0px" }}>Info</h1>
                        <form onSubmit={onLogout}>
                            <div>
                                <label htmlFor="username">User Name: </label>
                                <br />
                                <img
                                    className="icon-image"
                                    src="/img/approved-profile.svg"
                                    alt="call"
                                />
                                <input
                                    className="width8"
                                    type="text"
                                    readOnly
                                    placeholder="user name"
                                    value={props.name}
                                />
                            </div>
                            <div>
                                <label>status: </label>
                                <br />
                                <img
                                    className="icon-image"
                                    src="/img/safety.svg"
                                    alt="call"
                                />
                                <input
                                    placeholder="prevlage"
                                    type="text"
                                    value={"ADMIN"}
                                    readOnly
                                />
                            </div>
                            <br />
                            <img
                                className="icon-image"
                                src="/img/exit-sign.svg"
                                alt="call"
                            />
                            <input type="submit" value="Logout" />
                        </form>
                    </>
                ) : (
                    <>
                        <h1 style={{ margine: "2px", padding: "0px" }}>
                            Login
                        </h1>
                        <form onSubmit={onLogin}>
                            <div>
                                {/* <label htmlFor="username">User Name: </label> */}
                                <br />
                                <img
                                    className="icon-image"
                                    src="/img/unapproved-profile.svg"
                                    alt="call"
                                />
                                <input
                                    className="width8"
                                    type="text"
                                    id="username"
                                    name="username"
                                    placeholder="enter name"
                                    required
                                    autoFocus
                                    value={user}
                                    onChange={(e) => {
                                        setUser(e.target.value);
                                        seterr(0);
                                        seterr("");
                                    }}
                                />
                            </div>
                            <div>
                                {/* <label htmlFor="password">password: </label> */}
                                <br />
                                <img
                                    className="icon-image"
                                    src="/img/password.svg"
                                    alt="call"
                                />
                                <input
                                    required
                                    placeholder="enter password"
                                    type="password"
                                    name="pasword"
                                    value={password}
                                    onChange={(e) => {
                                        setpassword(e.target.value);

                                        seterr(0);
                                        seterr_m("");
                                    }}
                                />
                            </div>
                            <br />
                            <img
                                className="icon-image"
                                src="/img/log-in.svg"
                                alt="call"
                            />
                            <input type="submit" value="Login" />
                        </form>
                    </>
                )}
            </div>
            <div className="border2">
                <p>Designed and deployed by </p>
                <p>
                    <b>Yared Bekuru</b>
                </p>
                <p>
                    Contact :&nbsp; &nbsp; :
                    <a href={`tel:+251933060604`}>
                        <img
                            className="b-image"
                            src="/img/phone.png"
                            alt="call"
                        />
                    </a>{" "}
                    : &nbsp; +251933060604
                </p>
            </div>
        </div>
    );
}
