import React, { useEffect, useRef, useState } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
// import { Url } from "../url";
function SimpleSearch(props) {
    // const searchT = ["plate", "phone"];
    // const searchU = ["CAR", "OWNER"];
    // const [minL, setMinL] = useState(1);

    const navigate = useNavigate();
    const [searchResult, setSearchResult] = useState([
        {
            car_id: "-",
            owner_id: "null",
            plate_no: "null",
            phone_no: "null",
        },
    ]);
    const [valueS, setValueS] = useState("");
    const [searchFor, setSearchFor] = useState("CAR");
    const [searchBy, setSearchBy] = useState("NUM");
    const searchBoxFocus = useRef();
    // const onFlip = (what) => {
    //     // setSearchFor(searchFor === searchT[0] ? searchT[1] : searchT[0]);
    //     const i = searchFor === "plate" ? 1 : 2;
    //     setMinL(i);
    //     setSearchResult([
    //         {
    //             car_id: "-",
    //             owner_id: "null",
    //             plate_no: "null",
    //             phone_no: "null",
    //         },
    //     ]);
    //     setValueS("");
    // };
    const { selected } = props;
    //set searchFor and search by
    const search_function = (what) => {
        // console.log(what);
        if (what === "CAR") setSearchFor("CAR");
        else if (what === "OWNER") setSearchFor("OWNER");
        if (what === "ID") setSearchBy("ID");
        else if (what === "NUM") setSearchBy("NUM");
        setSearchResult([
            {
                car_id: "-",
                owner_id: "null",
                plate_no: "null",
                phone_no: "null",
            },
        ]);
        setValueS("");
    };
    //LIVE search on the search box
    const find = async (number) => {
        setValueS(number);
        // setValueS(e.target.value);
        // const i = searchFor === "plate" ? 1 : 2;
        // setMinL(i);
        if (number.length < 1) {
            setSearchResult([
                {
                    car_id: "-",
                    owner_id: "null",
                    plate_no: "null",
                    phone_no: "null",
                },
            ]);
        }
        // if (no.length > minL)
        else {
            try {
                const url = `${props.myToken}/search/${searchFor}/${searchBy}/${number}`;
                // console.log(url);
                let res;
                // = await
                axios
                    .get(url)
                    .then((rrr) => {
                        res = rrr;
                        if (rrr.data.length > 0) {
                            setSearchResult(rrr.data);
                        } else {
                            setSearchResult([
                                {
                                    car_id: "-",
                                    owner_id: "-",
                                    plate_no: "not found",
                                    phone_no: "not found",
                                },
                            ]);
                        }
                    })
                    .catch((error) => {
                        // console.log("this is the error !");
                        // console.log(error.response.status);
                        if (error.response.status == "401") {
                            //window.location.reload(false);
                            navigate("/login");
                            // window.location = "/login";
                        }
                    });
                // console.log(res);
            } catch (err) {
                console.log(err);
            }
        }
    };
    const sendSelected = (value) => {
        // console.log(value);
        selected(value.car_id, value.owner_id, searchFor);
    };
    const uploadID = (id, idof) => {
        // console.log(id, idof);
        // console.log(props);
        props.onidchange(id, idof);
    };
    useEffect(() => {
        searchBoxFocus.current.focus();
    }, [searchFor, searchBy]);
    return (
        <div className="simpleSearch">
            <div className="paddingT1">
                <div>
                    <b>Search-</b>
                    <b
                        className={
                            searchFor === "CAR" ? "findTypeActive" : "findType"
                        }
                        onClick={() => {
                            search_function("CAR");
                        }}
                    >
                        CAR
                    </b>
                    <b
                        className={
                            searchFor === "OWNER"
                                ? "findTypeActive"
                                : "findType"
                        }
                        onClick={() => {
                            search_function("OWNER");
                        }}
                    >
                        OWNER
                    </b>
                </div>
                <div className={searchFor === "CAR" ? "-" : "off"}>
                    <b>by----</b>
                    <b
                        className={
                            searchBy === "NUM" ? "findTypeActive" : "findType"
                        }
                        onClick={() => {
                            search_function("NUM");
                        }}
                    >
                        PLATE
                    </b>
                    <b
                        className={
                            searchBy === "ID" ? "findTypeActive" : "findType"
                        }
                        onClick={() => {
                            search_function("ID");
                        }}
                    >
                        ID
                    </b>
                </div>
                <div className={searchFor === "OWNER" ? "-" : "off"}>
                    <b>by----</b>
                    <b
                        className={
                            searchBy === "NUM" ? "findTypeActive" : "findType"
                        }
                        onClick={() => {
                            search_function("NUM");
                        }}
                    >
                        PHONE
                    </b>
                    <b
                        className={
                            searchBy === "ID" ? "findTypeActive" : "findType"
                        }
                        onClick={() => {
                            search_function("ID");
                        }}
                    >
                        ID
                    </b>
                </div>
            </div>
            <div className=" searchBox">
                <div className="searchText">
                    <form
                        onSubmit={(event) => {
                            event.preventDefault();
                            sendSelected(searchResult[0]);
                        }}
                    ></form>
                    <input
                        type={
                            searchBy == "NUM" && searchFor == "CAR"
                                ? "text"
                                : "number"
                        }
                        pattern={
                            searchBy == "NUM" && searchFor == "CAR"
                                ? "[A-Z]?[0-9]{5}"
                                : "[1,9]"
                        }
                        value={valueS}
                        onChange={(e) => {
                            find(e.target.value);
                        }}
                        placeholder={
                            searchBy === "ID"
                                ? "enter ID"
                                : searchFor === "OWNER"
                                ? 911223344
                                : "A12345"
                        }
                        ref={searchBoxFocus}
                        autoFocus
                    />
                    <span
                        onClick={() => {
                            setValueS("");
                            setSearchResult([
                                {
                                    car_id: "-",
                                    owner_id: "null",
                                    plate_no: "null",
                                    phone_no: "null",
                                },
                            ]);
                            searchBoxFocus.current.focus();
                        }}
                    >
                        X
                    </span>
                </div>
            </div>
            <div className={searchResult[0].owner_id === "null" ? "off" : "-"}>
                <div id="resultListContainer">
                    <Link className="Link" to={props.link}>
                        <div
                            className={
                                searchResult[0].phone_no === "not found"
                                    ? "off"
                                    : "resultList"
                            }
                            style={{ backgroundColor: "gray" }}
                        >
                            <span
                                style={{
                                    display: "inline-block",
                                    width: "3em",
                                    padding: "0px 1px",
                                    backgroundColor: "rgba(0,0,0,0.2)",
                                }}
                            >
                                ID
                            </span>
                            <span>
                                {searchFor === "CAR"
                                    ? "plate No."
                                    : "phone No."}
                            </span>
                        </div>
                    </Link>
                    {searchResult[0].phone_no !== "not found" ? (
                        Array.isArray(searchResult) ? (
                            searchResult.map((val, ind) => (
                                <div
                                    className="resultList pointer"
                                    key={ind}
                                    onClick={() => {
                                        sendSelected(val);
                                    }}
                                >
                                    <Link className="Link" to={props.link}>
                                        <span
                                            style={{
                                                display: "inline-block",
                                                color: "ButtonShadow",
                                                width: "3em",
                                                padding: "0px 1px",
                                                backgroundColor:
                                                    "rgba(0,0,0,0.4)",
                                            }}
                                        >
                                            {searchFor === "CAR"
                                                ? val.car_id
                                                : val.owner_id}
                                        </span>
                                        <span>
                                            {searchFor === "CAR"
                                                ? val.plate_no
                                                : val.phone_no}
                                        </span>
                                        {/* </div> */}
                                    </Link>
                                    <Link
                                        style={{ float: "right" }}
                                        className="searchText"
                                        to={"/main/home/new/customer"}
                                        onClick={() => {
                                            uploadID(
                                                searchFor === "CAR"
                                                    ? val.car_id
                                                    : val.owner_id,
                                                searchFor
                                            );
                                        }}
                                    >
                                        <span>x</span>
                                    </Link>
                                </div>
                            ))
                        ) : null
                    ) : (
                        <div
                            className={
                                searchResult[0].owner_id === "-" ? "-" : "off"
                            }
                        >
                            <Link
                                style={{
                                    textDecoration: "none",
                                    color: "green",
                                }}
                                to={"/main/home/new/customer"}
                                className="resultList pointer"
                            >
                                + ADD NEW +
                            </Link>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
export default SimpleSearch;
