import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";

import "./Costomer.css";
// import { Url } from "../../url";
// import SingleCostomer from "./SingleCostomers";
// import TableHeader from "./TableHeader";
// import TableFooter from "./Tablefooter";

// import costomers from "./customerSample";

function UnpaidJobs(props) {
    const navigate = useNavigate();
    const cellHeader = [
        "job ID",
        "Car ID",
        "km",
        "recieved date",
        "type",
        "p_cash",
        "p_cheque",
        "p_transfer",
        "p_credit",
        "replaced parts",
        "delivered date",
        "technician",
        "note",
    ];
    const [tableV, setTableV] = useState([
        {
            1: "",
            2: "Server",
            3: "",
            4: "unavailable",
        },
        {},
    ]);
    const [Url, setUrl] = useState(props.myToken);
    useEffect(() => {
        // console.log("effect");
        try {
            axios
                .get(`${Url}/unpaid`)
                .then((response) => {
                    setTableV(response.data);
                    // console.log(response);
                })
                .catch((error) => {
                    // console.log("this is the error !");
                    // console.log(error.response.status);
                    if (error.response.status == "401") {
                        //window.location.reload(false);
                        navigate("/login");
                        // window.location = "/login";
                    }
                });
            // console.log(cellValue);
        } catch (err) {
            console.log(err);
        }
    }, []);
    const uploadID = (id, idof) => {
        // console.log(id, idof);
        props.onidchange(id, "jobs");
    };

    return (
        <div className="allCostomers">
            <h1 style={{ color: "rgb(200,0,0)" }}>Unpaid Jobs</h1>
            <table className="table1">
                <thead>
                    <tr className="thead">
                        <td>-</td>
                        {Array.isArray(cellHeader)
                            ? cellHeader.map((val, ind) => (
                                  <td key={ind}>{val}</td>
                              ))
                            : null}
                    </tr>
                </thead>
                <tbody>
                    {Array.isArray(tableV)
                        ? tableV.map((val, ind) => (
                              <tr
                                  key={ind}
                                  className={
                                      ind === 20
                                          ? "darkRow"
                                          : ind % 2 === 0
                                          ? "darkRow"
                                          : ""
                                  }
                              >
                                  <td
                                      onClick={() => {
                                          uploadID(val.job_id, "jobs");
                                      }}
                                  >
                                      <div id="edit">
                                          <Link to={"/main/home/new/job"}>
                                              --
                                          </Link>
                                      </div>
                                  </td>
                                  {Object.keys(val).map((key, ind) => (
                                      <td key={ind}>{val[key]} </td>
                                  ))}
                              </tr>
                          ))
                        : null}
                </tbody>
            </table>
        </div>
    );
}

export default UnpaidJobs;
