import React from "react";
import { useEffect, useState } from "react";
import "./home.css";

function Search(props) {
    const { typelist, settypelist, type, setType } = props;

    const List1 = {
        type1: [
            { id: 1, val: "Service" },
            { id: 2, val: "Inspection" },
            { id: 3, val: "General service" },
            { id: 1, val: "Electric maintenance" },
            { id: 1, val: "Oil change" },
            { id: 1, val: "Body part" },
            { id: 1, val: "Engine overall" },
        ],
        technitian: [
            { id: 1, val: "Wendosen" },
            { id: 2, val: "Kirubel" },
            { id: 3, val: "Meki" },
            { id: 1, val: "Tilahun tola" },
            { id: 2, val: "Belay" },
            { id: 3, val: "Temesgen" },
            { id: 3, val: "Tilahun" },
            { id: 3, val: "Daniel" },
        ],
    };
    useEffect(() => {
        // console.log(typelist);
    });
    // const [typelist, settypelist] = useState(0);
    // console.log(props);
    return (
        <div className={typelist ? "select" : "offscreen"}>
            <legend
                className="clear pointer"
                onClick={() => {
                    settypelist(0);
                }}
            >
                ×
            </legend>
            <legend>Select</legend>
            {List1[type].map((item, ind) => (
                <div
                    key={ind}
                    onClick={() => {
                        setType(item?.val, type);
                        settypelist(0);
                    }}
                >
                    {item?.val}
                </div>
            ))}
        </div>
    );
}
export default Search;
